:root {
  --grid-rows: 1;
  --grid-cols: 1;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body, div {
  justify-content: center;
  align-items: center;
  display: flex;
}

body {
  height: 100vh;
  width: 100vw;
  flex-direction: row-reverse;
  gap: 60px;
  position: fixed;
}

sup {
  vertical-align: top;
  font-size: .7rem;
  position: relative;
  top: -.3rem;
}

.bottom-grp__rules {
  line-height: 1.3em;
}

.bottom-grp__rules-header {
  font-size: 1.2rem;
}

.calc-grp__input {
  font-size: 1rem;
}

.calc-grp__output, .calc-grp__grid-buttons {
  font-size: 2rem;
}

button {
  font-size: 1.5rem;
}

.calc-grp, .bottom-grp, .bottom-grp__rules {
  flex-direction: column;
}

.calc-grp {
  gap: 30px;
}

.calc-grp__header {
  text-align: center;
  text-decoration: underline;
}

.bottom-grp {
  gap: 20px;
  margin-top: 20px;
}

.bottom-grp__rules {
  width: 600px;
  white-space: normal;
  border: 1px solid #000;
  gap: 10px;
  padding: 10px;
}

.bottom-grp__rules ul {
  margin-left: 20px;
}

.bottom-grp__move-calc-btn {
  cursor: pointer;
  background-color: #b4b4b4;
  border: 1px solid #000;
  padding: 1rem;
}

.calc-grp__calc {
  min-height: 450px;
  min-width: 325px;
  flex-direction: column;
  position: relative;
}

.calc-grp__display, .calc-grp__btns {
  width: 100%;
  position: absolute;
}

.calc-grp__display {
  height: 20%;
  background-color: #5ec496;
  border: 1px solid #000;
  flex-direction: column;
  gap: 5px;
  padding: 15px;
  top: 0;
}

.calc-grp__input, .calc-grp__output {
  width: 100%;
  text-overflow: ellipsis;
  display: block;
  overflow: hidden;
}

.calc-grp__input {
  height: 30%;
}

.calc-grp__output {
  text-align: right;
  height: 50%;
}

.calc-grp__btns {
  height: 80%;
  justify-content: initial;
  align-items: initial;
  grid-template-rows: repeat(var(--grid-rows), 1fr);
  grid-template-columns: repeat(var(--grid-cols), 1fr);
  display: grid;
  bottom: 0;
}

.calc-grp__grid-buttons {
  border: 1px solid #000;
}

.calc-grp__grid-buttons:hover {
  background-color: #007fff66;
}

.calc-grp__grid-buttons:active {
  background-color: #007fff33;
}

@media (max-width: 600px) {
  body {
    height: calc(var(--vh, 1vh) * 100);
    background-color: #282828;
    gap: 0;
  }

  .calc-grp {
    height: 100%;
    width: 100%;
  }

  .calc-grp__calc {
    width: 100%;
    height: 80%;
    min-width: 0;
    min-height: 0;
    background-color: #fff;
  }

  .calc-grp__header {
    display: none;
  }

  .calc-grp__grid-buttons {
    font-size: 1.5em;
  }

  .bottom-grp {
    margin: 0;
    display: none;
  }
}

@media (max-width: 1000px) {
  body {
    flex-direction: column !important;
  }

  .bottom-grp__move-calc-btn {
    display: none;
  }
}

